import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Game from '../views/Games.vue'
import Over from '../views/Over.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Games',
    name: 'Games',
    component: Game
  },
  {
    path: '/Over',
    name: 'Over',
    component: Over
  }
]

const router = new VueRouter({
  routes
})

export default router
