import Vue from 'vue'
import Vuex from 'vuex'
// 需要使用插件一次
Vue.use(Vuex)


// 对外暴露 Store 的一个实例
export default new Vuex.Store({
    // 实现Vuex仓库模块式开发存储数据
    // home模块的小仓库
 state : {
    // state中数据默认初始值不能乱写 根据接口返回值初始化
    count:0
   
},
// mutations 是唯一修改state的地方
 mutations : {
   increment(state){
       state.count++
   }
},
// actions 用户处理派发action的地方，可以写异步语句，自己的逻辑
 actions : {
  
},
// 计算属性
 getters : {

}
})