<script>
export default {
  name: "Tapu",
  data() {
    return {
      // 文本
      TaPuText: "",
      // 鼠标移动
      TaPuMousemove: "",
      // 点击
      TaPuClick: "",
      // 文本类
      dhk: "dhk",
      dhkId:'',
      eyeL: "./images/img/tapu/眼睛↗.png",
      eyeR: "./images/img/tapu/眼睛↗.png",
      // 93式纯摸鱼显示与隐藏
      YU93:false,
      // 眼镜显示与隐藏
      MSK:false,
      // 类名
      tapudwd:'tapudwd',
      tapushowL:'tapushowL',
      tapushowL2:'tapushowL2',
      tapushowR:'tapushowR',
      tapushowR2:'tapushowR2',
      taputou:'taputou',
      tapujiaoL:'tapujiaoL',
      tapujiaoL2:'tapujiaoL2',
      tapujiaoR:'tapujiaoR',
      tapujiaoR2:'tapujiaoR2',
      weijinL:'weijinL',
      weijinR:'weijinR',
      taputouB:'taputouB',
      taputouA:'taputouA',
      tpText:''
    };
  },
  mounted() {
    let TaPus = this;
    this.$bus.$on("tpText", (text) => {
      clearInterval(this.tpText)
      this.dhk = "dhk dhkOP";
      this.TaPuText=text
      this.tpText=setTimeout(() => {
        
        this.dhk = "dhk";
      }, 4444);
    });
    let ran = parseInt(Math.random() * 3);
    function MSK() {
      TaPus.MSK=true
      TaPus.YU93=false
      TaPus.taputouB='msk2'
      TaPus.taputouA='msk1'
      TaPus.weijinL='weijinL weijinLm'
      TaPus.weijinR='weijinR weijinRm'
      TaPus.tapudwd='tapudwd mskdwd'
      TaPus.taputou='taputou msktou'
      TaPus.tapujiaoR='tapujiaoR mskfr'
      TaPus.tapujiaoR2='tapujiaoR2 mskfr2'
      TaPus.tapujiaoL='tapujiaoL mskfl'
      TaPus.tapushowR='tapushowR msksr'
      TaPus.tapushowR2='tapushowR2 msksr2'
      TaPus.tapushowL='tapushowL msksl'
      TaPus.tapushowL2='tapushowL2 msksl2'
      TaPus.dhkId=''
    }
    function BAILAN() {
      TaPus.MSK=false
      TaPus.YU93=true
      TaPus.tapudwd='tapudwd tapudwdBailan'
      TaPus.tapushowR='tapushowR tapushowRBailan'
      TaPus.tapushowR2='tapushowR2 tapushowR2Bailan'
      TaPus.tapushowL='tapushowL tapushowLBailan'
      TaPus.tapushowL2='tapushowL2 tapushowL2Bailan'
      TaPus.taputou='taputou taputouBailan'
      TaPus.tapujiaoR='tapujiaoR tapujiaoRBailan'
      TaPus.tapujiaoL='tapujiaoL tapujiaoLBailan'
      TaPus.weijinR='weijinR weijinRBailan'
      TaPus.weijinL='weijinL weijinLBailan'
      TaPus.taputouB='bailan'
      TaPus.taputouA='bailan2'
      TaPus.dhkId='dhkId'
    }
    function PUTO() {
      TaPus.MSK=false
      TaPus.YU93=false
      TaPus.tapushowR='tapushowR tapushowRPUTO'
      TaPus.tapushowR2='tapushowR2 tapushowR2PUTO'
      TaPus.tapushowL='tapushowL tapushowLPUTO'
      TaPus.tapushowL2='tapushowL2 tapushowL2PUTO'
      TaPus.taputouB='puto1'
      TaPus.taputouA='puto2'
      TaPus.dhkId=''
      
    }
    switch (ran) {
      case 0:
        MSK();
        break;
      case 1:
        BAILAN();
        break;
      case 2:
        PUTO();
        break;
    }
    // TaPuMousemove
    let tapudwd = document.querySelector(".tapudwd");
    document.addEventListener(
      "mousemove",
      (this.TaPuMousemove = (e) => {
        const eyePL = document.getElementById("tapueyeL");
        const eyePR = document.getElementById("tapueyeR");
        let Lx = eyePL.getBoundingClientRect().left + eyePL.clientWidth / 2;
        let Ly = eyePL.getBoundingClientRect().top + eyePL.clientHeight / 2;
        let Rx = eyePR.getBoundingClientRect().left + eyePR.clientWidth / 2;
        let Ry = eyePR.getBoundingClientRect().top + eyePR.clientHeight / 2;
        let Lxx = e.pageX - Lx + 50;
        let Lyy = e.pageY - Ly + 50;
        let Rxx = e.pageX - Rx + 50;
        let Ryy = e.pageY - Ry + 50;
        if (Lxx < 0 && Lyy < 0) {
          this.eyeL = "./images/img/tapu/眼睛↖.png";
        } else if (Lxx < 0 && Lyy > 0) {
          this.eyeL = "./images/img/tapu/眼睛↙.png";
        } else if (Lxx > 0 && Lyy < 0) {
          this.eyeL = "./images/img/tapu/眼睛↗.png";
        } else if (Lxx > 0 && Lyy > 0) {
          this.eyeL = "./images/img/tapu/眼睛↘.png";
        }
        if (Rxx < 0 && Ryy < 0) {
          this.eyeR = "./images/img/tapu/眼睛↖.png";
        } else if (Rxx < 0 && Ryy > 0) {
          this.eyeR = "./images/img/tapu/眼睛↙.png";
        } else if (Rxx > 0 && Ryy < 0) {
          this.eyeR = "./images/img/tapu/眼睛↗.png";
        } else if (Rxx > 0 && Ryy > 0) {
          this.eyeR = "./images/img/tapu/眼睛↘.png";
        }
      })
    );
    let flagClick = true;
    tapudwd.addEventListener(
      "click",
      (this.TaPuClick = (e) => {
        let ranText = parseInt(Math.random() * 5);
        if (flagClick) {
          flagClick = false;
          TaPus.dhk='dhk dhkOP'
          setTimeout(() => {
            TaPus.dhk='dhk'
            setTimeout(() => {
              flagClick = true;
            }, 500);
          }, 4000);
          if (ran == 0) {
            TPTEXT(
              `今天你看<a href="https://www.bilibili.com/bangumi/media/md5793/?spm_id_from=666.25.b_6d656469615f6d6f64756c65.2">《珈百璃的堕落》</a>了吗?`,
              `JavaScript Yes!`,
              `这个眼镜?是天真前辈送我的,据说戴上能加9点防御力😊`,
              `😊😊😊`,
              `我是神圣的天使，千咲·塔普莉丝·修格贝尔(｀・ω・´)!!!`
            );
          } else if (ran == 1) {
            TPTEXT(
              `( ´_ゝ｀)？摆烂了...`,
              `( ´_ゝ｀)？摸鱼...`,
              `╮(￣▽￣)╭今天你寄了吗...`,
              `一只珈百璃..两只珈百璃...三只珈百璃...四只.......(¦3【▓▓】...`,
              "没有天真前辈的涩()照片看,要死掉了...(´；ω；`)"
            );
          } else {
            TPTEXT(
              `━━━∑(ﾟ□ﾟ*川━`,
              `(ﾟДﾟ≡ﾟдﾟ)!?`,
              `Σ(ﾟдﾟ;)`,
              `(｡･ω･｡)`,
              `_(:з」∠)_`
            );
          }
          function TPTEXT(TEXTA, TEXTB, TEXTC, TEXTD, TEXTE) {
            switch (ranText) {
              case 0:
                TaPus.TaPuText = TEXTA;
                break;
              case 1:
                TaPus.TaPuText = TEXTB;
                break;
              case 2:
                TaPus.TaPuText = TEXTC;
                break;
              case 3:
                TaPus.TaPuText = TEXTD;
                break;
              case 4:
                TaPus.TaPuText = TEXTE;
                break;
            }
          }
        }
      })
    );
  },
  beforeDestroy() {
    document.removeEventListener('mousemove',this.TaPuMousemove)
  },
};
</script>

