  <div class="home">
    <img class="logo" alt="Vue logo" src="../assets/logo.png" />
    <img class="logo" alt="PIXIJS logo" style="left:220px; top:10px;" src="./images/PIXIJS.png" />
    <img id="HomeBack" :src="HomeBack" alt="" />
    <video
      id="HomeVideo"
      :src="HomeVideos"
      autoplay="autoplay"
      muted="muted"
      loop="loop"
    ></video>
    <audio
      id="bgmLit"
      :src="HomeAudios"
      loop="loop"
      autoplay="autoplay"
    ></audio>
    <Tapu></Tapu>
    <div
      class="center"
      ref="center"
      :style="{ transform: `scale(${centerNum})` }"
    >
      <img src="./images/GG.jpg" alt="" />
    </div>
    <div
      ref="levels1"
      :class="levels1"
      id="level"
      @click="levelClick(HomeBackground[arrIndex].fun,'EASY')"
      @mouseover="levelMouseover"
      @mouseout="levelMouseout"
      :style="{width: `${level1W}px`}"
    >
      <p class="levelP">EASY</p>
    </div>
    <div
      ref="levels2"
      :class="levels2"
      id="level2"
      @click="levelClick(HomeBackground[arrIndex].fun2,'NORMAL')"
      @mouseover="level2Mouseover"
      @mouseout="level2Mouseout"
      :style="{width: `${level2W}px`}"
    >
      <p class="levelP">NORMAL</p>
    </div>
    <div
      ref="levels3"
      :class="levels3"
      id="level3"
      @click="levelClick(HomeBackground[arrIndex].fun3,'HARD')"
      @mouseover="level3Mouseover"
      @mouseout="level3Mouseout"
      :style="{width: `${level3W}px`}"
    >
      <p class="levelP">HARD</p>
    </div>
    <div class="ReBox">
      <Background></Background>
      <div :class="HomeBackgroundName" id="HomeBackgroundName">{{ MName }}</div>
      <div :class="pName" id="pName">{{ PName }}</div>
      <div ref="box" :class="box">
        <div
          v-for="(item, index) in 8"
          :key="index"
          class="litbox"
          ref="litbox"
        >
          <div ref="Text" :class="`Text${item} Text`">
            <div class="testZ"></div>
            <div class="textUp">
              <img :ref="`HomeBack`" :src="HomeBackArr[item]" alt="" />
            </div>
            <div class="textDown">
              <img :ref="`HomeBack`" :src="HomeBackArr[item]" alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { GabEd, content, Genshin ,Heart_Pounding,Heart_Pounding2,WANTED} from "./images/content";
export default {
  name: "Home",
  data() {
    return {
      HomeBackground: [
        {
          name: "蓄意轰拳!",
          fun: "",
          fun2: "",
          fun3: "",
          bgm: "",
          bgmLit: "",
          back: "./images/HomeBack1.jpg",
          backm: "",
          fname: "蓄意轰拳!",
        },
        {
          name: "ハレルヤ☆エッサイム",
          fun: GabEd,
          fun2: '',
          fun3: '',
          bgm: "./images/富田美憂,大西沙織,大空直美 - ハレルヤ☆エッサイム.mp3",
          bgmLit:
            "./images/富田美憂,大西沙織,大空直美 - ハレルヤ☆エッサイム (clip).mp3",
          back: "./images/HomeGab1.png",
          backm: "",
          fname: "富田美憂,大西沙織,大空直美",
        },
        {
          name: "無人区-Vacuum Track#ADD8E6-",
          fun: content,
          fun2: '',
          fun3: '',
          bgm: "./images/无人区.mp3",
          bgmLit: "./images/无人区 (clip).mp3",
          back: "./images/HomeWRQ.png",
          backm: "",
          fname: "NoKANY",
        },
        {
          name: "Heart-Pounding Flight",
          fun: Heart_Pounding,
          fun2: Heart_Pounding2,
          fun3: '',
          bgm: "./images/TetraCalyx - Heart-Pounding Flight.mp3",
          bgmLit: "./images/TetraCalyx - Heart-Pounding Flight (clip).mp3",
          back: "./images/interlude_05.png",
          backm: "",
          fname: "TetraCalyx",
        },
        {
          name: "原什么神",
          fun: Genshin,
          fun2: '',
          fun3: '',
          bgm: "./images/原神 2022-01-05 04-56-20 (V0).mp3",
          bgmLit: "./images/原神 2022-01-05 04-56-20 (V0).mp3",
          back: "./images/PIXIV91988144.jpg",
          backm: "./images/原神 2022-01-05 04-56-20.mp4",
          fname: "1021",
        },
        {
          name: "WANTED!",
          fun: WANTED,
          fun2: '',
          fun3: '',
          bgm: "./images/門倉聡 - お寻ね者との戦い.mp3",
          bgmLit: "./images/門倉聡 - お寻ね者との戦い (clip).mp3",
          back: "./images/MMAX.png",
          backm: "",
          fname: "門倉聡",
        },
      ],
      HomeBack: "",
      // ↓这是寄寄子的图片链接
      HomeG: "-1465d25193900007.jpg",
      HomeVideos: "",
      HomeAudios: "",
      MName: "",
      PName: "",
      // 本数组用于根据HomeBackground的数据渲染轮播图图片 能看见的三个和即将登场的两个
      HomeBackArr: [],
      box: "box",
      levels1: "levels",
      levels2: "levels",
      levels3: "levels",
      pName: "",
      HomeBackgroundName: "",
      bgmTime: "",
      centerNum: 1,
      arrIndex: 0,
      ReClick:'',
      level1W:10,
      level2W:10,
      level3W:10,
      // 方法
      HomeTime:'',
      NullPointer:0
    };
  },
  computed: {},
  beforeDestroy() {
    clearInterval(this.bgmTime);
    document.removeEventListener('keydown',this.HomeKey)
    document.removeEventListener('click',this.ReClick)
  },
  mounted() {
     this.HomeTime = function(task,task2,timer,timer2) {
      return new Promise((resolve,reject)=>{
        setTimeout(() => {
          task()
          setTimeout(() => {
            task2()
          }, timer2);
        }, timer);
      })
    }
    let bgmLit = document.getElementById("bgmLit");
    let Homes = this;
    let deg = -50;
    let degLit = 50;
    let index = 1;
    let arr = Homes.HomeBackground;
    // 初始化
    Homes.HomeBackArr[1] = arr[0].back;
    Homes.HomeBackArr[2] = arr[1].back;
    Homes.HomeBackArr[8] = arr[arr.length - 1].back;
    Homes.HomeBack = arr[0].back;
    Homes.MName = arr[0].name;
    Homes.PName = arr[0].fname;
    let levelMouseover = false;
    let flag = 1;
    let levelKey = 1;
    let num;
    let bgmFlag = true;
    
    document.addEventListener(
      "keydown",
      (Homes.HomeKey = (e) => {
        if (e.key == "ArrowRight") {
          levelRe();
          index--;
          Homes.arrIndex--;
          if (index < 1) {
            index = 8;
          }
          if (Homes.arrIndex < 0) {
            Homes.arrIndex = arr.length - 1;
          }
          deg += 45;
          degLit -= 45;
        } else if (e.key == "ArrowLeft") {
          levelRe();
          index++;
          Homes.arrIndex++;
          if (index > 8) {
            index = 1;
          }
          if (Homes.arrIndex > arr.length - 1) {
            Homes.arrIndex = 0;
          }
          deg -= 45;
          degLit += 45;
        } else if (e.key == "ArrowDown" && levelMouseover) {
          Homes.levelAllMouseout();
          levelKey++;
          if (levelKey > 3) {
            levelKey = 1;
          }
        } else if (e.key == "ArrowUp" && levelMouseover) {
          Homes.levelAllMouseout();
          levelKey--;
          if (levelKey < 1) {
            levelKey = 3;
          }
        } else if (e.key == "Enter") {
         
          Homes.HomeBackgroundName = "HomeBackgroundName";
          Homes.pName = "pName";
          Homes.$refs.litbox[index - 1].style.transform = "scale(1.3)";
          flag++;
          levelHe();
          if (levelMouseover && flag == 3) {
            levelRe();
            switch (levelKey) {
              case 1:
                Homes.$refs.levels1.click()
                break;
              case 2:
                Homes.$refs.levels2.click()
                break;
              case 3:
                Homes.$refs.levels3.click()
                break;
            }
          }
        }
        switch (levelKey) {
          case 1:
            Homes.levelMouseover();
            break;
          case 2:
            Homes.level2Mouseover();
            break;
          case 3:
            Homes.level3Mouseover();
            break;
        }

        if (e.key == "ArrowRight" || e.key == "ArrowLeft") {
          // 触发键盘事件 轮播图转动 图片，背景，bgm更新
          console.log(arr[Homes.arrIndex].name);
          
          Homes.HomeBackArr[index] = arr[Homes.arrIndex].back;
          Homes.HomeBack = arr[Homes.arrIndex].back;
          Homes.HomeVideos = arr[Homes.arrIndex].backm;
          Homes.HomeBackArr[index == 8 ? 1 : index + 1] =
            arr[Homes.arrIndex == arr.length - 1 ? 0 : Homes.arrIndex + 1].back;
          Homes.HomeBackArr[index >= 7 ? 2 : index + 2] =
            arr[Homes.arrIndex >= arr.length - 2 ? 1 : Homes.arrIndex + 2].back;
          Homes.HomeBackArr[index == 1 ? 8 : index - 1] =
            arr[Homes.arrIndex == 0 ? arr.length - 1 : Homes.arrIndex - 1].back;
          Homes.HomeBackArr[index <= 2 ? 7 : index - 2] =
            arr[Homes.arrIndex <= 1 ? arr.length - 2 : Homes.arrIndex - 2].back;
          Homes.MName = arr[Homes.arrIndex].name;
          Homes.PName = arr[Homes.arrIndex].fname;
          Homes.$refs.box.style.transform = `rotateZ(${deg}deg)`;
          Homes.$refs.Text.forEach((item) => {
            item.style.transform = `rotateZ(${degLit}deg)`;
          });
          Homes.HomeAudios = arr[Homes.arrIndex].bgmLit;
          let ran = parseInt(Math.random() * 2);
          switch (arr[Homes.arrIndex].name) {
            case '原什么神':
                ran==1?Homes.$bus.$emit("tpText", '我超!!! 原!!!'):Homes.$bus.$emit("tpText", '寄寄子都没有反应,你这BGM算是寄了!')
              break;
            case '蓄意轰拳!':
                ran==1?Homes.$bus.$emit("tpText", '吃了吗？ 没吃的话，吃我一拳!'):Homes.$bus.$emit("tpText", '蓄意轰拳!')
              break;
            case 'ハレルヤ☆エッサイム':
                Homes.$bus.$emit("tpText", '如果打到一半没音符了，请不要慌张，这不是bug，只是谱没写完...')
              break;
          
          }
          
        }
        // 右下角飞鹰的大小跟随音频变化 最小值为1 如果寄寄子不寄了，那一定是音频的问题!
        let output = new Uint8Array(1);
        let AudioContext = new window.AudioContext();
        let analyser;
        let source;
        if (bgmFlag) {
          bgmFlag = false;
          analyser = AudioContext.createAnalyser();
          source = AudioContext.createMediaElementSource(bgmLit);
          source.connect(analyser);
          analyser.connect(AudioContext.destination);
          Homes.bgmTime = setInterval(() => {
            analyser.getByteFrequencyData(output);
            num = output / 175 < 1 ? 1+output / 700 : output / 175;
            Homes.centerNum = num;
          }, 0);
        }
      })
    );
    // 重置动画
    function levelRe() {
      Homes.pName = "";
      Homes.HomeBackgroundName = "";
      Homes.$refs.litbox[index - 1].style.transform = "scale(1)";
      Homes.HomeBackgroundName = "";
      Homes.pName = "";
      Homes.levelAllMouseout();
      flag = 1;
      levelMouseover = false;
      let level2D = ()=>Homes.level2W=10
      let level3D = ()=>Homes.level3W=10
      Homes.level1W=10
      let leveReD=async()=>{
        await Homes.HomeTime(level2D,level3D,50,50)
      }
      leveReD()
    }
    function levelHe() {
      levelMouseover = true;
      Homes.level1W=250
      let level2A = ()=>{
        Homes.level2W=300
        }
      let level3A = ()=>{
        Homes.level3W=320
        }
      let leveReA=async()=>{
        await Homes.HomeTime(level2A,level3A,50,50)
      }
      leveReA()
      
    }
    document.addEventListener('click',Homes.ReClick=e=>{
      levelRe();
    })
    
  },
  methods: {
    // 鼠标移入事件 移除事件 改变颜色 | 点击事件 跳路由
    levelMouseover() {
      this.levels1 = "levels levels1";
    },
    levelMouseout() {
      this.levels1 = "levels";
    },
    level2Mouseover() {
      this.levels2 = "levels levels2";
    },
    level2Mouseout() {
      this.levels2 = "levels";
    },
    level3Mouseover() {
      this.levels3 = "levels levels3";
    },
    level3Mouseout() {
      this.levels3 = "levels";
    },
    levelAllMouseout() {
      this.levelMouseout();
      this.level2Mouseout();
      this.level3Mouseout();
    },
    levelClick(level,text) {
      
      let isLevel = ()=> {
        if(level!=''){
          this.$bus.$emit("cgDorU", "down")
        }else{
          this.NullPointer++
          this.NullPointer==4?this.$bus.$emit("tpText", "选中单位未完成，下次一定....为什么未完成? 因为我是懒狗"):this.$bus.$emit("tpText", "选中单位未完成，下次一定!")
          this.NullPointer==4?this.NullPointer=0:null
        }
      }
      let levelRoute = ()=>{
        if(level!=''){
          this.$router.push({
              name: "Games",
              params: {
                bgm: this.HomeBackground[this.arrIndex].bgm,
                fun: level,
                back: this.HomeBackground[this.arrIndex].back,
                name: this.HomeBackground[this.arrIndex].name,
                level:text
              },
            });
            this.$bus.$emit("cgDorU", "up");
        }
        
      }
      let levelRun = async()=>{
        await this.HomeTime(isLevel,levelRoute,0,1800)
      }
      levelRun()
    },
    
  },
};
</script>

