        // continuity
        function continuityG(direction, delay) {
          switch (direction) {
            // delay 定时器时间 RX RY BX BY LOR   398 增加的画布半径   72 blob的半径
            case 1:
              continuity(delay, 368 + 398, 368, 0 + 398 - 36, 398, "L");
              break;
            case 2:
              continuity(delay, 592 + 398, 368, 1024 + 398 + 36, 398, "R");
              break;
          }
        }
        function continuity(delay, RX, RY, BX, BY, LOR) {
          let blob = new PIXI.Sprite(
            PIXI.loader.resources["./images/测试兔子.png"].texture
          );
          let perfect = new PIXI.Text("Perfect", styleP);
          let great = new PIXI.Text("Great", styleG);
          blob.evens = 0;
          let even = new PIXI.Text(`${blob.evens}`, styleG);
          even.alpha = 0;

          blob.x = BX;
          blob.y = BY;
          blob.vy = 6;
          blob.width = 144;
          blob.height = 224;
          blob.alpha = 0;
          LOR == "L"
            ? continuityBlobs[0].push(blob)
            : continuityBlobs[1].push(blob);
          blob.anchor.set(0.5, 0.5);
          app.stage.addChild(blob);
          app.stage.addChild(even);

          let speed = 6;
          let speedY = 5;
          // 每秒执行60次
          blob.Pef = false;
          blob.Gre = false;
          let mm = 0;
          let flag = true;
          app.ticker.add((delta) => {
            speedY -= 0.1;
            if (speedY <= 0) {
              speedY = 0;
            }
            blob.x += LOR == "L" ? blob.vy : -blob.vy;
            blob.y += speedY;
            blob.alpha += 0.02;
            if (blob.alpha > 1) {
              blob.alpha = 1;
            }

            if (LOR == "L") {
              if (blob.x > 512 + 398) {
                app.stage.removeChild(blob);
                if (mm == 0) {
                  mm++;
                  HPAdd();
                  comboRe();
                }
              }
            } else if (LOR == "R") {
              if (blob.x < 512 + 398) {
                app.stage.removeChild(blob);
                if (mm == 0) {
                  mm++;
                  HPAdd();
                  comboRe();
                }
              }
            }

            if (blob.vy == 0) {
              if (flag) {
                flag = false;
                setTimeout(() => {
                  blob.echo = false;
                  app.stage.removeChild(blob);
                  LOR == "L"
                    ? continuityBlobs[0].shift()
                    : continuityBlobs[1].shift();
                  conP = true;
                }, delay);
              }

              if (blob.Pef == true) {
                app.stage.addChild(perfect);
                even.text = `${blob.evens}`;
                even.alpha = 1;
                perfect.y -= 4;
                perfect.alpha -= 0.02;
                setTimeout(() => {
                  app.stage.removeChild(even);
                }, delay);
                if (perfect.alpha <= 0) {
                  app.stage.removeChild(perfect);
                }
              } else if (blob.Gre == true) {
                app.stage.addChild(great);
                even.text = `${blob.evens}`;
                even.alpha = 1;
                great.y -= 4;
                great.alpha -= 0.02;
                setTimeout(() => {
                  app.stage.removeChild(even);
                }, delay);
                if (great.alpha <= 0) {
                  app.stage.removeChild(great);
                }
              }
            } else {
              perfect.x = blob.x - 96;
              perfect.y = blob.y;
              great.x = blob.x - 96;
              great.y = blob.y;
              even.x = blob.x - 12;
              even.y = blob.y - 96;
            }
          });
          blob.echo = true;
        }
        // generate
        function generateG(direction) {
          switch (direction) {
            case 1:
              generate(400 - 32 + 398 - 2, 400 - 1 - 32, 0 + 398, 0, "L", "U");
              break;
            case 2:
              generate(
                400 - 32 + 398 - 2,
                624 + 3 - 32,
                0 + 398,
                1024,
                "L",
                "D"
              );
              break;
            case 3:
              generate(
                624 - 32 + 398 + 2,
                400 - 2 - 32,
                1024 + 398,
                0,
                "R",
                "U"
              );
              break;
            case 4:
              generate(
                624 - 32 + 398 + 2,
                624 + 3 - 32,
                1024 + 398,
                1024,
                "R",
                "D"
              );
              break;
          }
        }
        function generate(RX, RY, BX, BY, LOR, UOD) {
          let blob = new PIXI.Sprite(
            PIXI.loader.resources["./images/测试五角星2.png"].texture
          );
          let blobG = new PIXI.Sprite(
            PIXI.loader.resources["./images/测试五角星2外圈.png"].texture
          );
          let perfect = new PIXI.Text("Perfect", styleP);
          let great = new PIXI.Text("Great", styleG);

          blob.width = 64;
          blob.height = 64;
          blob.anchor.set(0.5, 0.5);
          blob.x = BX;
          blob.y = BY;
          blob.vy = 6;
          blob.circular = true;
          blob.diameter = true;

          blobG.width = 64;
          blobG.height = 64;
          blobG.anchor.x = 0.5;
          blobG.anchor.y = 0.5;
          blobG.x = 0;
          blobG.y = 0;
          blobG.vy = 6;
          blob.Tick = Tick;

          if (LOR == "L" && UOD == "U") {
            generateBlobs[0].push(blob);
          } else if (LOR == "L" && UOD == "D") {
            generateBlobs[1].push(blob);
          } else if (LOR == "R" && UOD == "U") {
            generateBlobs[2].push(blob);
          } else if (LOR == "R" && UOD == "D") {
            generateBlobs[3].push(blob);
          }
          app.stage.addChild(blob);

          blob.Pef = false;
          blob.Gre = false;
          // 每秒执行60次
          app.ticker.add((delta) => {
            blob.x += LOR == "L" ? blob.vy : -blob.vy;
            blob.y += UOD == "U" ? blob.vy : -blob.vy;
            blob.rotation += LOR == "L" ? 0.1 : -0.1;
            blobG.x = blob.x;
            blobG.y = blob.y;
            blobG.rotation = blob.rotation;

            // 速度为0 说明被击中 消除
            if (blob.vy == 0) {
              app.stage.addChild(blobG);
              blobG.width += 6;
              blobG.height += 6;
              blobG.alpha -= 0.05;
              if (blob.Pef == true) {
                app.stage.addChild(perfect);
                perfect.y -= 4;
                perfect.alpha -= 0.02;
              } else if (blob.Gre == true) {
                app.stage.addChild(great);
                great.y -= 4;
                great.alpha -= 0.02;
              }
              if (blobG.width > 160) {
                app.stage.removeChild(blobG);
              }
              if (blobG.width > 180) {
                app.stage.removeChild(perfect);
                app.stage.removeChild(great);
              }
            } else {
              perfect.x = blob.x - 96;
              perfect.y = blob.y;
              great.x = blob.x - 96;
              great.y = blob.y;
            }
          });
        }
        // 幽灵 音符 待定.... 塔罗牌 随机效果...
        // 断连击
