          let keyEFlag = true;
          let keyDo = true;
          let keyTe = true;
          document.addEventListener(
            "keydown",
            (GameThis.GameKeyDownApp1 = (e) => {
              let ran = parseInt(Math.random() * 2);
              if (HP > 315) return;
              if ((e.key == "b" || e.key == "B") && keyOFlag) {
                keyOFlag = false;
                keyO = 1;
                setTimeout(() => {
                  keyO = 0;
                }, 10);
              }
              if ((e.key == "c" || e.key == "C") && keyEFlag) {
                keyEFlag = false;
                keyE = 1;
                setTimeout(() => {
                  keyE = 0;
                }, 10);
              }
              if (keyO == 1 && keyE == 1) {
                keyDo = false;
                keyTe = false;

                TimeOuO();
                a = 5;
              }
              if (keyTe && keyDo) {
                if (e.key == "b" || e.key == "B") {
                  keyTe = false;
                  TimeOuO();
                  a = ran == 0 ? 1 : 2;
                } else if (e.key == "c" || e.key == "C") {
                  keyTe = false;
                  TimeOuO();
                  a = ran == 0 ? 3 : 4;
                }
              }
              document.addEventListener(
                "keyup",
                (GameThis.GameKeyUpApp1 = (e) => {
                  if (HP > 315) return;
                  if (e.key == "b" || e.key == "B") {
                    keyO = -1;
                    keyOFlag = true;
                  } else if (e.key == "c" || e.key == "C") {
                    keyE = -1;
                    keyEFlag = true;
                  }
                  if (keyE == -1 && keyO == -1) {
                    keyTe = true;
                    keyDo = true;
                    a = 0;
                    fuKe();
                    keyE = 0;
                    keyO = 0;
                  }
                  if ((e.key == "b" || e.key == "B") && keyDo) {
                    keyTe = true;
                    a = 0;
                    fuKe();
                  } else if ((e.key == "c" || e.key == "C") && keyDo) {
                    keyTe = true;
                    a = 0;
                    fuKe();
                  }
                })
              );
            })
          );

          let FK1;
          let FK2;
          let FK3;
          let FK4;
          function TimeOuO() {
            clearInterval(FK1);
            clearInterval(FK2);
            clearInterval(FK3);
            clearInterval(FK4);
          }
          setTimeout(() => {
            fuKe();
          }, 0);
          function fuKe() {
            TimeOuO();
            if (HP < 315) {
              a = 0;
              FK1 = setTimeout(() => {
                a = -1;
                FK2 = setTimeout(() => {
                  a = -2;
                  FK3 = setTimeout(() => {
                    a = -3;
                  }, 300);
                }, 300);
              }, 300);
              FK4 = setInterval(() => {
                fuKe();
              }, 1200);
            }
          }
        }
