                    keyDo = true;
                    a = 0;
                    fuKe();
                    keyE = 0;
                    keyO = 0;
                  }
                  if ((e.key == "h" || e.key == "H") && keyDo) {
                    keyTe = true;
                    a = 0;
                    fuKe();
                  } else if ((e.key == "d" || e.key == "D") && keyDo) {
                    keyTe = true;
                    a = 0;
                    fuKe();
                  }
                })
              );
