          let FK2;
          let FK3;
          let FK4;
          function TimeOuO() {
            clearInterval(FK1);
            clearInterval(FK2);
            clearInterval(FK3);
            clearInterval(FK4);
          }
          setTimeout(() => {
            fuKe();
          }, 0);
          function fuKe() {
            TimeOuO();
            if (HP < 315) {
              a = 0;
              FK1 = setTimeout(() => {
                a = -1;
                FK2 = setTimeout(() => {
                  a = -2;
                  FK3 = setTimeout(() => {
                    a = -3;
                  }, 300);
                }, 300);
              }, 300);
              FK4 = setInterval(() => {
                fuKe();
              }, 1200);
            }
