<template>
  <div id="Over">
    <Tapu></Tapu>
    <Background></Background>
      <div class="OVERText">
        OVER
      </div>
    <div class="MB">
      <div class="OverName">
        <img class="OverNameImg" src="./images/箭头特效.png" alt="">
        
        <div class="difficulty">
          {{levels}}
        </div>
        <div class="OName">{{names}}</div>
        <div class="fraction">
          得分&nbsp;&nbsp; {{fraction}}
        </div>
      </div>
      <div class="OverFun">
        <img class="OverFunImg" src="./images/箭头特效.png" alt="">
        <div class="Accuracy">
          准确率&nbsp;{{(Accuracy)}}%
        </div>
        <div class="OverCombos">
          最大连击&nbsp;&nbsp; {{MaxCombos}}
        </div>
        <div class="OverP">
          PERFECT&nbsp;&nbsp; {{perfect}}
        </div>
        <div class="OverG">
          GREAT&nbsp;&nbsp; {{great}}
        </div>
        <div class="OverM">
          MISS&nbsp;&nbsp; {{Miss}}
        </div>
      </div>
      <div class="OverYu">
        <img class="OverYuImg" src="./images/箭头特效.png" alt="">
        <div class="fractionMax">
          最高分&nbsp;&nbsp; {{fraction}}
        </div>
      </div>
      <div class="continue" @click="ReHome">
        继续
      </div>
      <div class="re" @click="ReGames">
        重新开始
      </div>
    </div>
    <div v-if="$route.params.G=='G'" :class="MB" id="G" @click="GG">
      <p :class="GClass">寄</p>
    </div>
    <div :class="levelse">
      {{level}}
    </div>
  </div>
</template>

<script>
export default {
  name:'Over',
  data() {
    return {
      names:'',
      perfect:0,
      great:0,
      Miss:0,
      // 准确率
      // Accuracy:0,
      // 选择难度
      difficulty:'',
      // 评级
      level:'',
      // 最大连击
      MaxCombos:0,
      // 分数
      fraction:0,
      // 类名
      levelse:'levelse',
      // 事件
      HomeOrGame:'',
      GClass:'MBG',
      MB:'MB',
      levels:''
    }
  },
  beforeDestroy() {
    // 移除事件
    document.removeEventListener('keydown',this.HomeOrGame)
  },
  mounted(){
    let Overs = this
    if(this.$route.params.names!=undefined){
      window.localStorage.setItem('names',this.$route.params.names)
      window.localStorage.setItem('perfect',this.$route.params.perfect)
      window.localStorage.setItem('great',this.$route.params.great)
      window.localStorage.setItem('Miss',this.$route.params.Miss)
      // window.localStorage.setItem('Accuracy',this.$route.params.Accuracy)
      window.localStorage.setItem('difficulty','EASY')
      window.localStorage.setItem('MaxCombos',this.$route.params.MaxCombos)
      window.localStorage.setItem('fraction',this.$route.params.fraction)
    }
    this.names=window.localStorage.getItem('names')
    this.perfect=window.localStorage.getItem('perfect')*1
    this.great=window.localStorage.getItem('great')*1
    this.Miss=window.localStorage.getItem('Miss')*1
    // this.Accuracy=parseFloat(window.localStorage.getItem('Accuracy'))
    this.difficulty=window.localStorage.getItem('difficulty')
    this.levels=this.$route.params.level
    this.levelse='levelse'
    if(this.Accuracy==100){
      this.level='S'
      this.levelse='levelse SSR'
    }else if(this.Accuracy>=95){
      this.level='S'
      this.levelse='levelse SR'
    }else if(this.Accuracy>=90){
      this.level='S'
    }else if(this.Accuracy>=80){
      this.level='A'
    }else if(this.Accuracy>=70){
      this.level='B'
    }else if(this.Accuracy>=60){
      this.level='C'
    }else{
      this.level='寄'
    }
    this.MaxCombos=window.localStorage.getItem('MaxCombos')
    this.fraction=window.localStorage.getItem('fraction')
    this.bgm=window.localStorage.getItem('bgm')
    this.fun=JSON.parse(window.localStorage.getItem('fun'))
    this.back=window.localStorage.getItem('back')
    document.addEventListener('keydown',Overs.HomeOrGame=(e)=>{
      if(e.key=='Enter'){
         Overs.ReHome()
      }else if(e.key=='r'||e.key=='R'){
         Overs.ReGames()
      }
    })
   
  },
  methods:{
    ReGames(){
       this.$bus.$emit("cgDorU", "down");
        setTimeout(() => {
          this.$router.push({name:'Games',params:{bgm:this.bgm,fun:this.fun,back:this.back,name:this.names}})
          this.$bus.$emit("cgDorU", "up");
        }, 800);
    },
    ReHome(){
      this.$bus.$emit("cgDorU", "down");
        setTimeout(() => {
          this.$router.push({ path: "/" });
          this.$bus.$emit("cgDorU", "up");
        }, 800);
    },
   GG(){
     this.MB='MB MBGC'
   }
  },
  computed:{
    Accuracy:{
      get(){
        return ((this.perfect*30 + this.great*10)/((this.Miss+this.great+this.perfect)*30)*100).toFixed(1)
      }
    }
  }
}
</script>

