<template>
  <div :class="cg">
    <img src="./images/背景.png" alt="" />
  </div>
</template>
<script>
export default {
    name:'CG',
    data() {
      return {
        cg:'cg'
      }
    },
    mounted() {
      this.$bus.$on('cgDorU',(CG)=>{
        CG=='up'?this.CGup():this.CGdown()
      })
    },
    methods: {
        CGdown(){
            this.cg='cg cgDown'
        },
        CGup(){
            this.cg='cg'
        }
    },
};
</script>
<style>
html {
  perspective: 800px;
}
.cgDown{
transform:rotateX(0deg) translateZ(150px) translateX(-50%) !important;
}
.cg {
  transform-style: preserve-3d;
  width: 1920px;
  height: 1080px;
  position: absolute;
  transition: all .5s;
  transform: rotateX(90deg) translateZ(150px) translateX(-50%);
  transform-origin: -50% -50%;
  z-index: 999;
    left: 50vw;
    top: 0;
}
.cg > img {
  width: 1921px;
  height: 1080px;
  position: absolute;
}
</style>